@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    color: #971111;
}

body {
    font: 400 14px 'Dancing Script', sans-serif;
    background: #F0F0F5;
    -webkit-font-smoothing: antialiased;
}

input, textarea {
    font: 400 18px Dancing+Script, sans-serif;
}

.link {
    cursor: pointer;
    font: 700 22px 'Dancing Script', sans-serif;
    width: 100%;
    height: 60px;
    background: #971111;
    border: 0;
    border-radius: 8px;
    color: #FFF;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    line-height: 60px;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
}

.link:hover {
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.main-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}
.main-container section.home {
    width: 100%;
    max-width: 350px;
    margin-right: 10px;
}

.main-container section.home h1 {
    font-size: 32px;
    margin-top: 100px;
    margin-bottom: 32px;
}


